.navbar{
    width: 100%;
    height: 65px;
    background-color: rgb(0, 0, 0);
    color: aqua;
    display: flex;
    flex-direction: row;
   
}

.navbar .nav-div{
    width: calc(100%/3);
    display: flex ;
    justify-content: center;
    align-items: center ;
}

.navbar .nav-div.list{
    justify-content: space-evenly;
}



.navbar .nav-div .menu{
    width: 100%;
    justify-content: flex-start;
    padding: 20px;
}